import React from 'react';
import { ErrorBoundary, StyledTableCell } from 'shared/components';
import { DepartmentType, LastMessageAuthorType } from 'shared/typings/dashboard.types';
import UnreadMessagesCount from 'shared/components/UnreadMessagesCount';
import { useAppState } from 'shared/state';
import { LastMessageView } from 'shared/components/LastMessageView';

type DashboardLatestType = {
	department: DepartmentType;
	lastMessageAuthor: LastMessageAuthorType;
	lastMessageText: string;
	lastMessageType: string;
	caseId: number;
	onClickHandler: () => void;
};

function DashboardLatest({
	department,
	caseId,
	lastMessageAuthor,
	lastMessageText,
	lastMessageType,
	onClickHandler
}: DashboardLatestType) {
	const { unreadMessagesCount } = useAppState();
	const hasUnread = unreadMessagesCount?.[caseId];

	return (
		<StyledTableCell onClick={onClickHandler}>
			<div>
				<p className="mb-1.5 truncate text-sm font-semibold">{department?.name}</p>
				<div className="flex items-center">
					{lastMessageAuthor ? (
						<LastMessageView
							last_message_type={lastMessageType}
							last_message_text={lastMessageText}
							last_message_author={lastMessageAuthor}
						/>
					) : null}
					<UnreadMessagesCount hasUnread={hasUnread} />
				</div>
			</div>
		</StyledTableCell>
	);
}

export default function DashboardLatestActivity(props: DashboardLatestType) {
	return (
		<ErrorBoundary>
			<DashboardLatest {...props} />
		</ErrorBoundary>
	);
}
