import { StandardPageLayout } from 'shared/layouts';
import { useUnreadCaseTab } from 'shared/hooks';
import { Helmet } from 'react-helmet';
import { ErrorBoundary } from 'shared/components';
import React from 'react';
import DashbordTable from './components/DashbordTable';
import BusinessDetails from './components/business-details/BusinessDetails';
import DashboardMap from './components/map/DashboardMap';

function DashboardInner() {
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('Dashboard');
	return (
		<StandardPageLayout>
			<div className={'dashboard'}>
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title> KENNEKT | User profile </title>
					</Helmet>
				)}
			</div>
			<div className="dashboard__inner">
				<h2 className="mb-6 text-xl font-bold dark:text-white md:text-3xl">Dashboard</h2>
				<DashbordTable />
				<div className="mt-8 flex flex-col justify-between lg:flex-row">
					<BusinessDetails />
					<DashboardMap />
				</div>
			</div>
		</StandardPageLayout>
	);
}

export function Dashboard() {
	return (
		<ErrorBoundary>
			<DashboardInner />
		</ErrorBoundary>
	);
}
