import React from 'react';
import { ErrorBoundary, Loader } from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import DashboardMapView from './DashboardMapView';
import { useLoadAll } from 'shared/hooks';

function DashboardMapCom() {
	const { companyId } = useAppState();
	const { reloadEntityAll } = useLoadAll();

	const reloadMapRequest = () => {
		reloadEntityAll({
			entity: 'onlineUsers',
			name: `onlineUsers-${companyId}`,
			url: `${companyId}/my/company/online/`
		});
	};
	return (
		<div className="w-full lg:ml-6 lg:w-6/12">
			{companyId ? (
				<EntityContainer.All
					entity="onlineUsers"
					name={`onlineUsers-${companyId}`}
					url={`${companyId}/my/company/online/`}
					params={{}}
				>
					{({ items, isFetched }) => {
						const onlineClients = items as any;
						return (
							<div className="relative">
								<DashboardMapView
									reloadMapRequest={reloadMapRequest}
									onlineClients={onlineClients}
								/>
								{!isFetched && <Loader tiled withOverlay />}
							</div>
						);
					}}
				</EntityContainer.All>
			) : null}
		</div>
	);
}

export default function DashboardMap() {
	return (
		<ErrorBoundary>
			<DashboardMapCom />
		</ErrorBoundary>
	);
}
