import React from 'react';
import { ErrorBoundary, StyledTableRow } from 'shared/components';
import clsx from 'clsx';
import DashboardLatestActivity from './DashboardLatestActivity';
import DashboardCaseInfo from './DashboardCaseInfo';
import { DashboardTableItemRequestPropsType } from 'shared/typings/dashboard.types';
import DashboardItemDep from './DashboardItemDep';
import DashboardItemClient from './DashboardItemClient';
import { useRouter } from '../../../shared/hooks';

function DashboardTableItem({
	id,
	department,
	createdDate,
	lastMessageAuthor,
	lastMessageText,
	caseNumber,
	status,
	priority,
	lastMessageType,
	subDepartment
}: DashboardTableItemRequestPropsType) {
	const { history } = useRouter();

	const openDashboardCaseHandler = () => {
		const actionUrl = {
			pathname: '/case-messages',
			search: `?case=${id}&status=${status.id}`
		};

		history.push(actionUrl);
	};

	return (
		<StyledTableRow
			className={clsx('dashboard-item cursor-pointer transition hover:bg-primary-200', {
				'is-critical': priority === 'CRITICAL',
				'is-normal': priority === 'NORMAL',
				'is-high': priority === 'HIGH',
				'is-low': priority === 'LOW'
			})}
		>
			{/*<DashboardItemClient onClickHandler={openDashboardCaseHandler} />*/}
			<DashboardLatestActivity
				caseId={id}
				department={department}
				lastMessageAuthor={lastMessageAuthor}
				lastMessageText={lastMessageText}
				lastMessageType={lastMessageType}
				onClickHandler={openDashboardCaseHandler}
			/>

			<DashboardItemDep
				subDepartment={subDepartment}
				department={department}
				onClickHandler={openDashboardCaseHandler}
			/>

			<DashboardCaseInfo
				caseNumber={caseNumber}
				status={status}
				createdDate={createdDate}
				priority={priority}
				onClickHandler={openDashboardCaseHandler}
			/>
		</StyledTableRow>
	);
}

export function DashboardTableCustomItem(props: DashboardTableItemRequestPropsType) {
	return (
		<ErrorBoundary>
			<DashboardTableItem {...props} />
		</ErrorBoundary>
	);
}
