import React from 'react';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import { TableContainer, TableHead, TableBody } from 'shared/components/Elements';
import { DashboardTableCustomItem } from './DashboardTableItem';
import { DashboardTableItemRequest } from 'shared/typings/dashboard.types';

function DashboardTableInner() {
	const { companyId } = useAppState();
	const { page } = usePagination();

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity={'dashboard'}
					name={`Dashboard-${companyId}`}
					url={`/${companyId}/widgets/cases/as_business/`}
					params={{
						limit: 5,
						extra: {
							ordering: 'last_view_time'
						},
						page
					}}
				>
					{({ items, isFetched }) => {
						const requests = items.map((item: unknown) => item) as DashboardTableItemRequest[];
						return (
							<div>
								<div className="prime-tabs rounded-t-lg bg-kgrey-bg px-4 pt-4 pb-6 text-base font-medium dark:bg-primary-300 dark:text-white">
									Recent Case Activity
								</div>

								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '50%' }}>
													Latest Case Activity
												</StyledTableHeadCell>
												{/*<StyledTableHeadCell style={{ width: '29%' }}>Client</StyledTableHeadCell>*/}
												<StyledTableHeadCell style={{ width: '25%' }}>
													Department
												</StyledTableHeadCell>

												<StyledTableHeadCell style={{ width: '25%' }}>Case</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{requests.map((item: DashboardTableItemRequest) => {
												const {
													id,
													business_company,
													department,
													created_date,
													last_message_author,
													last_message_text,
													case_number,
													status,
													priority,
													last_message_type,
													sub_department
												} = item;
												return (
													<DashboardTableCustomItem
														key={id}
														id={id}
														subDepartment={sub_department}
														businessCompany={business_company}
														department={department}
														createdDate={created_date}
														lastMessageAuthor={last_message_author}
														lastMessageText={last_message_text}
														caseNumber={case_number}
														status={status}
														priority={priority}
														lastMessageType={last_message_type}
													/>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function DashboardTable() {
	return (
		<ErrorBoundary>
			<DashboardTableInner />
		</ErrorBoundary>
	);
}
