import React from 'react';
import { ErrorBoundary, Loader } from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import { BusinessDetailsType } from 'shared/typings/dashboard.types';
import CardDetailsInfo from '../../../../shared/components/CardDetailsInfo';
import { IconHouse, IconMembers, IconNotifications, IconUserDetail } from 'shared/icons';
import BusinessProgressBar from './BusinessProgressBar';
import CachedIcon from '@material-ui/icons/Cached';
import { useLoadOne } from 'shared/hooks';

function BusinessDetailsCom() {
	const { companyId } = useAppState();
	const { reloadEntityOne } = useLoadOne();

	const refreshDetailsRequest = () => {
		reloadEntityOne({
			primaryKey: '#businessDetails',
			entityId: '#businessDetails',
			entity: 'businessDetails',
			name: `businessDetails-${companyId}`,
			url: `${companyId}/my/company/dashboard/`
		});
	};
	return (
		<div className="mb-4 flex-grow lg:mb-0">
			{companyId ? (
				<EntityContainer.One
					primaryKey="#businessDetails"
					id="#businessDetails"
					entity="businessDetails"
					name={`businessDetails-${companyId}`}
					url={`${companyId}/my/company/dashboard/`}
					params={{}}
					relations={''}
				>
					{({ item, isFetched }) => {
						const detailsItem = item as BusinessDetailsType;
						return (
							<div className="relative rounded-10 bg-kgrey-bg px-3 py-4 dark:bg-kdark-lighter">
								<div className="mb-3 flex items-center justify-between px-1.5">
									<h3 className="text-base font-medium dark:text-white">Business Details</h3>
									<div className="cursor-pointer" onClick={refreshDetailsRequest}>
										<CachedIcon
											fontSize="small"
											className="inline-block h-4 w-4
											text-kgrey-2d2d2d
											dark:text-primary"
										/>
									</div>
								</div>
								<div className="mx-[-10px] flex flex-wrap">
									<div className="mx-2.5 mb-4">
										<CardDetailsInfo title={'Departments'} count={detailsItem.departments}>
											<IconHouse />
										</CardDetailsInfo>
									</div>
									<div className="mx-2.5 mb-4">
										<CardDetailsInfo title={'Clients'} count={detailsItem.clients}>
											<IconUserDetail />
										</CardDetailsInfo>
									</div>
									<div className="mx-2.5 mb-4">
										<CardDetailsInfo title={'News'} count={detailsItem.notifications}>
											<IconNotifications />
										</CardDetailsInfo>
									</div>
									<div className="mx-2.5 mb-4">
										<CardDetailsInfo title={'Staffs'} count={detailsItem.employee}>
											<IconMembers />
										</CardDetailsInfo>
									</div>
								</div>
								<BusinessProgressBar percent={detailsItem.percent} />
								{!isFetched && <Loader tiled withOverlay />}
							</div>
						);
					}}
				</EntityContainer.One>
			) : null}
		</div>
	);
}

export default function BusinessDetails() {
	return (
		<ErrorBoundary>
			<BusinessDetailsCom />
		</ErrorBoundary>
	);
}
