import React from 'react';

type UnreadMessagesCountType = {
	hasUnread: number | undefined | null;
};

function UnreadMessagesCount({ hasUnread }: UnreadMessagesCountType) {
	return (
		<>
			{hasUnread ? (
				<div className="ml-2 h-6 w-6 rounded-full bg-primary text-center text-sm font-semibold leading-6 text-white">
					{hasUnread}
				</div>
			) : null}
		</>
	);
}
export default UnreadMessagesCount;
