import { DepartmentType } from '../../typings/dashboard.types';
import { Avatar } from '@material-ui/core';

export const getDepartmentIcon = (department: DepartmentType, subDepartment: DepartmentType) => {
	if (subDepartment) {
		if (subDepartment.icon) {
			return (
				<Avatar
					className="inline-block"
					alt={subDepartment.name}
					src={subDepartment.icon?.thumbnail_150 || subDepartment.icon?.url}
				/>
			);
		} else {
			return (
				<Avatar className="inline-block" style={{ color: '#fff' }}>
					{subDepartment.name[0].toUpperCase()}
				</Avatar>
			);
		}
	} else if (department) {
		if (department.icon) {
			return (
				<Avatar
					className="inline-block"
					alt={department.name}
					src={department?.icon?.thumbnail_150 || department?.icon?.url}
				/>
			);
		} else {
			return (
				<Avatar className="inline-block" style={{ color: '#fff' }}>
					{department?.name[0].toUpperCase()}
				</Avatar>
			);
		}
	}
};
