import React from 'react';
import { ErrorBoundary, StyledTableCell } from 'shared/components';
import dayjs from 'dayjs';
import clsx from 'clsx';
import { StatusType } from 'shared/typings/dashboard.types';

type DashboardCaseInfoType = {
	caseNumber: number;
	status: StatusType;
	createdDate: string;
	priority: string;
	onClickHandler: () => void;
};

function DashboardCase({
	caseNumber,
	status,
	priority,
	createdDate,
	onClickHandler
}: DashboardCaseInfoType) {
	return (
		<StyledTableCell onClick={onClickHandler}>
			<div>
				<p className="mb-1 text-sm font-medium text-kgrey-dark-3">#{caseNumber}</p>
				<div
					className={clsx(
						'mb-1.5 inline-block h-5 rounded-full px-6 text-10px font-medium uppercase leading-5',
						{
							'bg-dpriority-high': priority === 'HIGH',
							'bg-dpriority-normal': priority === 'NORMAL',
							'bg-dpriority-low': priority === 'LOW',
							'bg-dpriority-critical': priority === 'CRITICAL'
						}
					)}
				>
					{status?.title}
				</div>
				<p className="text-xs font-medium leading-5">
					{dayjs(createdDate).format('MMMM DD hh:mm a')}
				</p>
			</div>
		</StyledTableCell>
	);
}

export default function DashboardCaseInfo(props: DashboardCaseInfoType) {
	return (
		<ErrorBoundary>
			<DashboardCase {...props} />
		</ErrorBoundary>
	);
}
