import React from 'react';
import CustomizedProgressBar from './CustomProgressBar';
import { getBusinessIcon } from 'shared/services/utils/getBusinessIcon';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { useAppState } from '../../../../shared/state';

export type BusinessProgressBarType = {
	percent: number;
};

function BusinessProgressBar({ percent }: BusinessProgressBarType) {
	const { company } = useAuth();
	const { isDarkMode } = useAppState();
	const companyIcon = company?.company.icon;
	const companyIconDark = company?.company.icon_dark;

	const iconUrl = isDarkMode
		? companyIconDark
			? companyIconDark?.thumbnail_150
			: companyIcon?.thumbnail_150
		: companyIcon?.thumbnail_150;
	return (
		<div className="rounded-10 bg-white p-3.5 dark:border-[0.5px] dark:border-zinc-700 dark:bg-kdark-lighter">
			<div className="mb-4 flex items-center">
				<div className="h-9 w-9">{getBusinessIcon(iconUrl, company?.company.name)}</div>
				<h4 className="ml-2.5 text-sm font-medium text-kgrey-2d2d2d dark:text-white">
					Profile Completeness
				</h4>
			</div>
			<CustomizedProgressBar percent={percent} />
		</div>
	);
}

export default BusinessProgressBar;
