import React from 'react';
import { StyledTableCell } from 'shared/components';
import { DepartmentType } from 'shared/typings/dashboard.types';
import { getDepartmentIcon } from 'shared/services/utils/getDepartmentAvatar';

export type DepartmentItemDepType = {
	department: DepartmentType;
	subDepartment: DepartmentType;
	onClickHandler: () => void;
};

function DashboardItemDep({ department, subDepartment, onClickHandler }: DepartmentItemDepType) {
	return (
		<StyledTableCell onClick={onClickHandler}>
			<div className="flex items-center">
				{getDepartmentIcon(department, subDepartment)}
				<span className="ml-2.5 truncate text-sm font-medium">{department?.name}</span>
			</div>
		</StyledTableCell>
	);
}

export default DashboardItemDep;
